.container {
  padding: 10px;
  max-width: 600px;
  margin: 0 auto;
}

.fixed-bar {
  position: sticky;
  top: 0;
  background-color: #fff;
  padding: 10px 0;
  display: flex;
  justify-content: space-around;
  border-bottom: 1px solid #ccc;
}

.fixed-bar button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: rgb(97, 119, 153);
  color: white;
}

.file-selection {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.file-item {
  margin-bottom: 10px;
}

.file-item button {
  margin-right: 10px;
}

.dimensions-section {
  margin-top: 20px;
}

.input-group {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 10px;
}

.input-group label {
  flex: 1 1 100%;
  margin-bottom: 5px;
}

.input-group input {
  flex: 1 1 45%;
  margin-right: 10px;
}

@media (min-width: 480px) {
  .input-group label {
    flex: 1 1 45%;
    margin-bottom: 0;
  }
}

.text-formatting {
  margin-top: 20px;
}

.checkbox-group {
  margin-bottom: 10px;
}

.color-picker {
  margin-bottom: 10px;
}

.radio-group {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 10px;
}

.radio-group label {
  margin-right: 10px;
}

.canvas-area {
  margin-top: 20px;
  text-align: center;
}

canvas {
  max-width: 100%;
  height: auto;
}

h2 {
  font-size: 17px;
  background-color: rgb(210, 210, 210);
  padding: 5px;
}

.names-list {
  margin-top: 20px;
}

.names-list-item {
  display: flex;
  align-items: center;
  padding: 2px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 2px;
  background-color: #f9f9f9;
}

.names-list-item input[type="checkbox"] {
  margin-right: 15px;
}

.names-list-item span {
  flex: 1;
  font-size: 16px;
}

.names-list-item button {
  padding: 5px 10px;
  background-color: #617799;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
}

.names-list-item button:hover {
  background-color: #4a6481;
}

.manual-names {
  display: flex;
  align-items: center;
  padding: 2px;
  border-radius: 5px;
  margin-bottom: 15px;
}

.manual-resize-container {
  overflow: visible;
}
